"use client";

import React, { useState, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { Globe, Check } from "lucide-react";
import { Button } from "@/components/ui/button";

interface Language {
  code: string;
  name: string;
  nativeName: string;
  flag: string;
}

const languages: Language[] = [
  {
    code: "de",
    name: "German",
    nativeName: "Deutsch",
    flag: "🇩🇪",
  },
  {
    code: "en",
    name: "English",
    nativeName: "English",
    flag: "🇬🇧",
  },
  {
    code: "ar",
    name: "Arabic",
    nativeName: "العربية",
    flag: "🇸🇦",
  },
  {
    code: "fr",
    name: "French",
    nativeName: "Français",
    flag: "🇫🇷",
  },
];

const LanguageSelector = () => {
  const [showDialog, setShowDialog] = useState(false);
  const [selectedLanguage, setSelectedLanguage] = useState<string>("de");

  useEffect(() => {
    const savedLanguage = localStorage.getItem("user-language");
    if (!savedLanguage) {
      // Show dialog after a short delay
      setTimeout(() => setShowDialog(true), 500);
    } else {
      setSelectedLanguage(savedLanguage);
    }
  }, []);

  const handleLanguageSelect = (languageCode: string) => {
    setSelectedLanguage(languageCode);
    localStorage.setItem("user-language", languageCode);
    localStorage.setItem("language-selected-at", new Date().toISOString());
    setShowDialog(false);
    
    // Reload page to apply language changes (in a real app, you'd use i18n)
    if (languageCode !== "de") {
      // For now, just close the dialog. In production, implement i18n
      console.log(`Language changed to: ${languageCode}`);
    }
  };

  return (
    <AnimatePresence>
      {showDialog && (
        <>
          {/* Backdrop */}
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black/60 backdrop-blur-sm z-[150]"
          />

          {/* Dialog */}
          <motion.div
            initial={{ opacity: 0, scale: 0.9, y: 20 }}
            animate={{ opacity: 1, scale: 1, y: 0 }}
            exit={{ opacity: 0, scale: 0.9, y: 20 }}
            transition={{ duration: 0.3, ease: "easeOut" }}
            className="fixed inset-0 m-auto w-[calc(100%-2rem)] max-w-xl h-fit max-h-[90vh] z-[151] bg-white rounded-2xl shadow-2xl overflow-auto"
          >
            {/* Header */}
            <div className="bg-gradient-to-br from-primary via-primary-dark to-primary text-white px-6 py-5 text-center relative overflow-hidden">
              <div className="absolute inset-0 bg-[radial-gradient(circle_at_50%_50%,rgba(255,255,255,0.1),transparent_60%)]"></div>
              <motion.div
                initial={{ scale: 0 }}
                animate={{ scale: 1 }}
                transition={{ delay: 0.2, type: "spring", stiffness: 200 }}
                className="relative flex flex-col items-center"
              >
                <div className="w-14 h-14 mx-auto mb-3 rounded-full bg-white/20 backdrop-blur-sm flex items-center justify-center border-2 border-white/30">
                  <Globe className="h-7 w-7 text-white" />
                </div>
                <h2 className="text-xl font-bold">Welcome! Willkommen! !مرحبا Bienvenue!</h2>
                <p className="text-white/90 text-sm mt-1">
                  Please select your preferred language
                </p>
              </motion.div>
            </div>

            {/* Content */}
            <div className="p-5">
              <div className="grid grid-cols-2 gap-3">
                {languages.map((language, index) => (
                  <motion.button
                    key={language.code}
                    initial={{ opacity: 0, y: 10 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 0.05 * index }}
                    onClick={() => handleLanguageSelect(language.code)}
                    className={`group relative p-4 rounded-xl border-2 transition-all duration-300 hover:shadow-lg ${
                      selectedLanguage === language.code
                        ? "border-primary bg-primary/5 shadow-md"
                        : "border-gray-200 hover:border-primary/50 bg-white"
                    }`}
                  >
                    <div className="flex items-center gap-3">
                      {/* Flag */}
                      <div className="text-3xl leading-none">{language.flag}</div>
                      
                      {/* Language Info */}
                      <div className="flex-1 text-left min-w-0">
                        <p className="text-base font-bold text-gray-900 group-hover:text-primary transition-colors truncate">
                          {language.nativeName}
                        </p>
                        <p className="text-xs text-gray-500">{language.name}</p>
                      </div>

                      {/* Check Mark */}
                      {selectedLanguage === language.code && (
                        <motion.div
                          initial={{ scale: 0 }}
                          animate={{ scale: 1 }}
                          className="w-6 h-6 rounded-full bg-primary flex items-center justify-center flex-shrink-0"
                        >
                          <Check className="h-4 w-4 text-white" />
                        </motion.div>
                      )}
                    </div>
                  </motion.button>
                ))}
              </div>

              {/* Continue Button */}
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 0.3 }}
                className="mt-5"
              >
                <Button
                  onClick={() => handleLanguageSelect(selectedLanguage)}
                  className="w-full text-base font-semibold py-5 bg-primary hover:bg-primary-dark shadow-lg hover:shadow-xl transition-all"
                  size="lg"
                >
                  Fortfahren / Continue
                </Button>
              </motion.div>

              {/* Note */}
              <p className="text-xs text-gray-400 text-center mt-3">
                Derzeit ist die Website auf Deutsch verfügbar.
              </p>
            </div>
          </motion.div>
        </>
      )}
    </AnimatePresence>
  );
};

export default LanguageSelector;
